/* ==========================================================================
  c-sidebar
  ========================================================================== */

.c-sidebar {
  background-color: $color-gray-50;
  height: 100%;
  width: 32rem;
  padding: 4rem 0;
  position: relative;
  min-width: 32rem;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.c-sidebar__menu {
  flex: 1;
  overflow: auto;
  padding: 0 2.4rem;
}

.c-sidebar__logo {
  display: inline-block;
  height: 3.2rem;
  padding: 0 2.4rem;
  margin-bottom: 4rem;

  img {
    height: 100%;
  }
}

.c-sidebar__item + .c-sidebar__item {
  margin-top: 0.4rem;
}

.c-sidebar__link {
  text-decoration: none;
  padding: 1.2rem 2.4rem;
  border-radius: 0.3rem;
  color: $color-gray-600;
  align-items: center;
  display: flex;
  position: relative;

  .o-svg-icon:first-child {
    margin-right: 1.6rem;
  }

  &:hover {
    background: #fff;
  }
}

.c-sidebar__item--active {
  > .c-sidebar__link {
    background: #fff;
    color: $color-gray-900;

    &:before {
      content: '';
      height: 100%;
      width: 0.4rem;
      position: absolute;
      border-radius: 0.3rem 0 0 0.3rem;
      left: 0;
      bottom: 0;
      background: $color-red-500;
    }
  }
}
